@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Ubuntu:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Ubuntu:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Montserrat",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.nitifi-sty {
  position: -webkit-sticky;
  position: sticky;
  top: 0;

  height: 40px;
  background: white;
  display: flex;

  justify-content: center;
  align-items: center;
  z-index: 20;
}
.static-nav {
  font-size: 15px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.contact-link {
  margin: 3px 10px;
}
.contact-link img {
  margin: 3px 10px;
}
.navbar-sty {
  position: -webkit-sticky;
  position: sticky;
  top: 40px;
  z-index: 20;
}
@media (max-width: 750px) {
  .nitifi-sty {
    height: 50px;
  }
  .navbar-sty {
    top: 50px;
  }
  .static-nav {
    flex-direction: column;
  }
}

:root {
  /* color */
  --main-color: rgb(0, 156, 246);
  --secondery-color: rgb(98, 98, 98);
  /* font */
  --ubuntu: "Ubuntu", sans-serif;
  --poppins: "Poppins", sans-serif;
  --montserrat: "Montserrat", sans-serif;
}
* {
  font-family: "Montserrat", sans-serif;
  font-family: var(--montserrat);
}
body::-webkit-scrollbar {
  width: 10px; /* width of the entire scrollbar */
}
body::-webkit-scrollbar-track {
  background: rgb(0, 0, 0); /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(0, 156, 246);
  background-color: var(--main-color); /* color of the scroll thumb */
  border-radius: 10px; /* roundness of the scroll thumb */
  border: 1px solid transparent; /* creates padding around scroll thumb */
}

.bg-navbar {
  background-color: rgb(0, 81, 173);
}
.brand-align {
  display: block;
}
.logo-name {
  color: rgb(0, 156, 246);
  color: var(--main-color);
  font-family: "Ubuntu", sans-serif;
  font-family: var(--ubuntu);
  font-size: 30px;
  font-weight: bold;
}
.home-container {
  position: relative;
  height: 70vh;
  width: 100%;
  background-image: url(/static/media/dev1.9add4eb9.jpg);
  background-size: cover;

  object-fit: cover;
}

.opacity-bg {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.774);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.title {
  width: calc(100vw / 2);
  font-size: 40px;
  text-align: center;
  color: white;
  font-weight: bold;
  margin: 25px;
  /* background-color: rgba(0, 0, 0, 0.89); */
}
@media (max-width: 1000px) {
  .title {
    width: calc(100vw / 1.3);
  }
}
.button {
  outline: none;
  border: none;
  padding: 10px;
  width: 200px;
  height: 50px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  background-color: rgb(0, 156, 246);
  background-color: var(--main-color);
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s;
}
.button:hover {
  background-color: rgb(5, 117, 221);
}

@media (max-width: 650px) {
  .home-container {
    background-position: center;
    height: 40vh;
  }
  .opacity-bg {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .title {
    font-size: 20px;
    margin: 25px;
  }
  .button {
    padding: 10px;
    width: 150px;
    height: 50px;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

/* About us */

.about-container {
  background-color: rgb(0, 156, 246);
  background-color: var(--main-color);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-content-div {
  padding: 10px;
  /* width: calc((99vw / 1.5)); */
  width: 90%;
  /* background-color: white; */
  border-radius: 15px;
  margin: 20px 0;
}

@media (max-width: 800px) {
  .about-content-div {
    width: calc((100vw / 1.2));
  }
}
.about-content-div p {
  margin: 10px 20px;
  /* color: var(--secondery-color); */
