  font-size: 20px;
  text-align: justify;
}

/* Section Title */
.section-div {
  display: inline;
  justify-content: center;
  margin: 40px 0;
}

.section-title {
  /* top: 0;
  left: 50%; */
  /* transform: translateX(-50%); */
  color: white;
  font-size: 35px;
  font-family: "Ubuntu", sans-serif;
  font-family: var(--ubuntu);
  font-weight: lighter;
  position: relative;
  display: inline;
  line-height: 0;
}
.section-title.blue {
  color: rgb(0, 156, 246);
  color: var(--main-color);
}
.section-title::after {
  content: "";
  position: absolute;
  text-align: center;
  width: 80%;
  left: 50%;
  height: 3px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border-radius: 50px;
}
.section-title.blue::after {
  background-color: rgb(0, 156, 246);
  background-color: var(--main-color);
}

/* carousel */

.carousel-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.all-carousel {
  width: 80%;
